import * as React from "react";
import * as _ from "lodash";
import { withRouter } from "react-router-dom";
import { hot } from "react-hot-loader";
import { Typography, Row, Col, Button, DatePicker, Avatar } from "antd";
import { useAuthState } from "react-firebase-hooks/auth";
import { useContext, useState, useEffect } from 'react';
import { auth, firestore } from "../../../firebase";
import firebaseApp from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import 'mapbox-gl/dist/mapbox-gl.css';
import { LoginUIContext } from "../../../providers/LoginUIProvider";
import { geohashForLocation } from "geofire-common";
import moment from 'moment';
import { Input } from 'antd';
import { Form, Radio } from 'antd';
import { Comment, Tooltip, List } from 'antd';

const { TextArea } = Input;

const ViewAllComments = ({ history, location, match, value }) => {

  const [user, loading, error] = useAuthState(auth);
  const [isLoginModalVisible, setIsLoginModalVisible] = useContext(LoginUIContext);
  const [comments, setComments] = useState([]);

  useEffect(() => {
    firestore.collection('events/' + value.doc.id + '/comments').get().then((snap) => {
      const c = [];
      for (const doc of snap.docs) {
        c.push({
          author: doc.get('ownerDisplayName'),
          avatar: doc.get('ownerPhotoUrl'),
          content: doc.get('value'),
          created: doc.get('created'),
          datetime: (<Tooltip title={moment(doc.get('created').toDate()).format('YYYY-MM-DD HH:mm:ss')}>
          <span>{moment(doc.get('created').toDate()).fromNow()}</span>
        </Tooltip>)
        })
      }
      setComments(c.sort(function(a, b) {
        if (a.created > b.created) {
          return 1; }
        if (a.created < b.created) {
          return -1; }
        return 0;
      }));
    })
  });

  return (
    <List
      dataSource={comments}
      itemLayout="horizontal"
      renderItem={props => <Comment {...props} />}
    />
  );
}

export default hot(module)(withRouter(ViewAllComments));