import * as React from "react";
import * as _ from "lodash";
import { withRouter } from "react-router-dom";
import { hot } from "react-hot-loader";
import { Typography, Row, Col, Button, DatePicker, Avatar } from "antd";
import { useAuthState } from "react-firebase-hooks/auth";
import { useContext, useState } from 'react';
import { auth, firestore } from "../../../firebase";
import firebaseApp from "firebase/app";
import "firebase/auth";
import "firebase/firestore";
import 'mapbox-gl/dist/mapbox-gl.css';
import { LoginUIContext } from "../../../providers/LoginUIProvider";
import { geohashForLocation } from "geofire-common";
import moment from 'moment';
import { Input } from 'antd';
import { Form, Radio } from 'antd';
import { Comment, Tooltip, List } from 'antd';

const { TextArea } = Input;

const CreateComment = ({ history, location, match, value }) => {

  const [user, loading, error] = useAuthState(auth);
  const [isLoginModalVisible, setIsLoginModalVisible] = useContext(LoginUIContext);
  const [form] = Form.useForm();
  const [comment, setComment] = useState('');
  const [submitting, setSubmitting] = useState(false);

  const handleChange = ({ comment }) => {
    if (comment) {
      setComment(comment);
    }
  };

  async function handleSubmit() {
    if (!comment) {
      return;
    }

    if (!loading && user) {
      setSubmitting(true);

      const obj = {
        value: comment,
        ownerPhotoUrl: user.photoURL,
        ownerDisplayName: user.displayName,
        ownerId: user.uid,
        created: new Date(),
      };
      const doc = await firestore.collection('events/' + value.doc.id + '/comments').add(obj);

      setSubmitting(false);
    }
    else {
      setIsLoginModalVisible(true);
    }
  };

  return (
    <Comment
      avatar={
        <Avatar
          src={user.photoURL}
          alt={user.displayName}
        />
      }
      content={
        <>
          <Form
            layout={"vertical"}
            initialValues={{ comment: comment }}
            form={form}
            onValuesChange={handleChange}
          >
            <Form.Item name="comment">
              <TextArea rows={4} />
            </Form.Item>
          </Form>
          <Button htmlType="submit" loading={submitting} onClick={handleSubmit} type="primary">
            Add Comment
          </Button>
        </>
      }
    />
  );
}

export default hot(module)(withRouter(CreateComment));