import * as React from "react";
import { createRef } from 'react';
import { hot } from "react-hot-loader";
import { withRouter } from "react-router-dom";
import './MapView.scss';
import { mapbox } from "../../mapbox";
import 'mapbox-gl/dist/mapbox-gl.css';
import { Typography, Row, Col, Button } from 'antd';
import CreateEvent from "./States/CreateEvent";
import { auth, firebase, firestore } from "../../firebase";
import { MapViewState, EventState, getDefaultEventState } from "./MapViewState";
import { ExploreMapState } from "./States/ExploreMapState";
const { Title } = Typography;
import { UserContext } from "../../providers/UserProvider"
import { MapStateContext } from "./States/MapState";
import { ViewEventMapState } from "./States/ViewEventMapState";
import { LoadingMapState } from "./States/LoadingMapState";

//https://github.com/firebase/firebaseui-web-react
//https://github.com/alex3165/react-mapbox-gl
class MapView extends React.Component<Record<string, unknown>, MapViewState> {
  public readonly state: MapViewState = {
    isCreatingEvent: false,
    canCreateEvent: false,
    eventState: getDefaultEventState(),
    mapBox: null,
    mapContainerRef: null,
    lng: -122.2946,
    lat: 37.1787,
    zoom: 14,
    allEvents: new Map<string, any>(),
  }

  static contextType = UserContext;
  private mapInstanceRef = createRef<HTMLDivElement>();
  private eventCreationZoomMinLevel = 11.7;
  private mapState = new LoadingMapState((a) => {
    this.mapState = a;
    this.setState(this.state);
  });

  constructor(props) {
    super(props);

    this.onMapMove = this.onMapMove.bind(this);
    this.onMapClick = this.onMapClick.bind(this);
    this.fillMapAfterLoadEvents = this.fillMapAfterLoadEvents.bind(this);

    firestore.collection('events').where('planned', '>', new Date()).orderBy('planned').get().then(this.fillMapAfterLoadEvents);
  }

  private fillMap() {
    const mapBox = this.state.mapBox;

    const features = [];
    for (const doc of this.state.allEvents.values()) {

      var points = doc.get('points')
      const lat = points[0].lat;
      const lng = points[0].lng;

      features.push({
        "type": "Feature",
        "properties":
          {
            'eventId': doc.id,
            'ownerId': doc.get('ownerId'),
            'displayType': this.context.id === doc.get('ownerId') ? "Mine" : "Other"
          },
        "geometry": {
          "type": "Point",
          "coordinates": [lng, lat, 0.0]
        }
      });
    }

    if (mapBox.getSource('routes')) {
      mapBox.getSource('routes').setData({
        "type": "FeatureCollection",
        "features": features,
      });
    }
    else {
      mapBox.addSource('routes',
        {
          'type': 'geojson',
          'data':
            {
              "type": "FeatureCollection",
              "features": features,
            },
          'cluster': true,
          'clusterRadius': 80,
          'clusterMaxZoom': 14,
        });
    }

    if (!mapBox.getLayer('route_circles')) {
      mapBox.addLayer({
        'id': 'route_circles',
        'type': 'circle',
        'source': 'routes',
        'filter': ['!=', 'cluster', true],
        'paint': {
          'circle-color': [
            'match',
            ['get', 'displayType'],
            'Mine',
            '#3887be',
            'Other',
            '#223b53',
            '#3887be'
          ],
          'circle-opacity': 0.8,
          'circle-radius': 12
        }
      });
    }

    function createDonutChart(props) {
      var total = props.point_count;
      var fontSize =
        total >= 1000 ? 22 : total >= 100 ? 20 : total >= 10 ? 18 : 16;
      var r = total >= 1000 ? 50 : total >= 100 ? 32 : total >= 10 ? 24 : 18;
      var r0 = Math.round(r * 0.6);
      var w = r * 2;

      var html =
        '<div><svg width="' +
        w +
        '" height="' +
        w +
        '" viewbox="0 0 ' +
        w +
        ' ' +
        w +
        '" text-anchor="middle" style="font: ' +
        fontSize +
        'px sans-serif; display: block">';

      html +=
        '<circle cx="' +
        r +
        '" cy="' +
        r +
        '" r="' +
        r0 +
        '" fill="#3887be" /><text dominant-baseline="central" transform="translate(' +
        r +
        ', ' +
        r +
        ')">' +
        total.toLocaleString() +
        '</text></svg></div>';

      var el = document.createElement('div');
      el.innerHTML = html;
      return el.firstChild;
    }

    // objects for caching and keeping track of HTML marker objects (for performance)
    var markers = {};
    var markersOnScreen = {};

    function updateMarkers() {
      var newMarkers = {};
      var features = mapBox.querySourceFeatures('routes');

      // for every cluster on the screen, create an HTML marker for it (if we didn't yet),
      // and add it to the map if it's not there already
      for (var i = 0; i < features.length; i++) {
        var coords = features[i].geometry.coordinates;
        var props = features[i].properties;
        if (!props.cluster) continue;
        var id = props.cluster_id;

        var marker = markers[id];
        if (!marker) {
          var el = createDonutChart(props);
          marker = markers[id] = new mapbox.Marker({
            element: el
          }).setLngLat(coords);
        }
        newMarkers[id] = marker;

        if (!markersOnScreen[id]) {
          marker.addTo(mapBox);
        }
      }
      // for every marker we've added previously, remove those that are no longer visible
      for (id in markersOnScreen) {
        if (!newMarkers[id]) markersOnScreen[id].remove();
      }
      markersOnScreen = newMarkers;
    }

    // after the GeoJSON data is loaded, update markers on the screen on every frame
    mapBox.on('render', function () {
      if (!mapBox.isSourceLoaded('routes')) return;
      updateMarkers();
    });

    const {eventId} = this.props.match.params;
    if(eventId) {
      this.mapState = ViewEventMapState.fromUrl(eventId, (a) => {
        this.mapState = a;
        this.setState(this.state);
      });
    }
    else {
      this.mapState = new ExploreMapState((a) => {
        this.mapState = a;
        this.setState(this.state);
      });
    }
    this.setState(this.state);
  }

  private fillMapAfterLoadEvents(snap) {
    for (const doc of snap.docs) {
      this.state.allEvents.set(doc.id, doc);
    }

    this.fillMap();
  }

  public componentDidMount() {
    const map = new mapbox.Map({
      container: this.mapInstanceRef.current,
      //style: 'mapbox://styles/mapbox/outdoors-v11',
      style: {
        version: 8,
        sources: {
          osm: {
            type: 'raster',
            tiles: ["https://tile.openstreetmap.org/{z}/{x}/{y}.png"],
            tileSize: 256,
            attribution: 'Map tiles by <a target="_top" rel="noopener" href="https://tile.openstreetmap.org/">OpenStreetMap tile servers</a>, under the <a target="_top" rel="noopener" href="https://operations.osmfoundation.org/policies/tiles/">tile usage policy</a>. Data by <a target="_top" rel="noopener" href="http://openstreetmap.org">OpenStreetMap</a>'
          }
        },
        layers: [{
          id: 'osm',
          type: 'raster',
          source: 'osm',
        }]
      },
      center: [this.state.lng, this.state.lat],
      zoom: this.state.zoom
    });

    map.on('move', (e) => this.onMapMove(map, e));
    map.on('click', (e) => this.onMapClick(e));

    map.addControl(new mapbox.NavigationControl());
    map.addControl(new mapbox.ScaleControl({ position: 'bottom-right' }));

    this.setState({ mapBox: map });
  }

  private onMapMove(map, e) {
    this.setState({
      lat: map.getCenter().lat.toFixed(4),
      lng: map.getCenter().lng.toFixed(4),
      zoom: map.getZoom().toFixed(2),
      canCreateEvent: this.eventCreationZoomMinLevel < this.state.zoom
    });
  }

  private createMapStateContext(): MapStateContext {
    return {
      viewState: this.state,
      setViewState: this.setState.bind(this),
      user: this.context,
      props: this.props,
      triggerStateChange: (newState) => { this.mapState = newState; this.setState(this.state); },
      updateMap: this.fillMap.bind(this)
    };
  }

  private onMapClick(e) {
    this.mapState = this.mapState.onMapClick(e, this.createMapStateContext());
    this.setState(this.state);
  }

  public render() {
    return (
      <>
        <Row>
          <Col span={18}>
            <div className='map-container' ref={this.mapInstanceRef} />
          </Col>
          <Col className='map-sidebar' span={6}>
            {this.mapState.render(this.createMapStateContext())}
          </Col>
        </Row>
      </>
    );
  }
}

declare let module: Record<string, unknown>;

export default hot(module)(withRouter(MapView));
